export default {
	data() {
		return {
			// 传递过来的排行榜数组
			data: []
		};
	},
	onLoad(e) {
		if (e.data) {
			this.data = JSON.parse(decodeURIComponent(e.data));
			// this.data = JSON.parse(decodeURIComponent(e.data))
			console.log(this.data);
		}
	}
};
